import React from 'react';
import PropTypes from 'prop-types';
import './Movie.css';

function Movie({year, title, summary, poster, genres}) {
  return (<div class='movie'>
    <img src={poster} alt={poster}></img>
    <div className='movie_data'>
    <h3 className='movie__title' style={{ backgroundColor:'blue'}}>{title}</h3>
    <h5 className='movie__year'>{year}</h5>
    <h3 className='movie__summary'>{summary}</h3>
    <ul className='genres'>{genres.map((genre, index) => (<li className='genres__genre'>{index}{genre}</li>))}</ul>
    </div>
  </div>);
}

Movie.propTypes = {
  id: PropTypes.number.isRequired,
  year: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  summary: PropTypes.string.isRequired,
  poster: PropTypes.string.isRequired,
  geners: PropTypes.arrayOf(PropTypes.string).isRequired,
}

export default Movie;